<template>
  <b-modal
    id="modal-update-expired"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="md"
    title="Cập nhật gia hạn hợp đồng"
  >
    <template #modal-footer>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-update-expired')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
      <button @click="store()" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
    </template>
    <div class="form-group">
      <label for="">Tên công ty</label>
      <input :value="contract.company_name" type="text" class="form-control" disabled />
    </div>

    <div class="form-group">
      <label for="">Ngày gia hạn</label>
      <date-picker v-model="expired.renew_date" :config="options"></date-picker>
      <!-- <input :value="expired.renew_date" type="text" class="form-control" /> -->
    </div>
  </b-modal>
</template>

<script>
import moment from "moment";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import axios from "axios";
export default {
  components: {
    datePicker,
  },
  data() {
    return {
      contract: {},
      expired: {
        contract_id: 0,
        renew_date: moment().format("DD/MM/YYYY HH:mm:ss"),
      },
      options: {
        format: "DD/MM/YYYY HH:mm:ss",
        useCurrent: false,
      },
    };
  },

  methods: {
    show(contract) {
      this.contract = contract;
      this.$bvModal.show("modal-update-expired");
    },
    async store() {
      this.expired.contract_id = this.contract.contract_id;
      await axios.put("contractexpired/update", this.expired).then((response) => {
        this.$emit("updateExpired", response);
        this.$bvModal.hide('modal-update-expired');
      });
    },
  },
};
</script>
