<template>
  <div>
    <h1 class="fw-bold title-page">Danh sách hợp đồng hết hạn</h1>
    <table class="table">
      <thead>
        <tr class="table-dark">
          <th width="10" class="rounded-start">#</th>
          <th width="35%">Tên công ty</th>
          <th>Mã số thuế</th>
          <th>Ngày BĐ</th>
          <th>Ngày KT</th>
          <th>Loại HĐ</th>
          <th>Gói cước</th>
          <th>Trạng thái</th>
          <th>Tổng tiền</th>
          <th width="10" class="rounded-end"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in contracts" :key="item.contract_id">
          <td>{{ item.contract_id }}</td>
          <td>
            <router-link
              :to="{
                name: 'contract-edit',
                params: {
                  id: item.contract_id,
                },
              }"
            >
              {{ item.company_name }}
            </router-link>
            <div v-if="item.contract_note">
              <i class="text-danger">{{ item.contract_note }}</i>
            </div>
          </td>
          <td>{{ item.company_taxcode }}</td>
          <td>{{ item.contract_date_start | moment("DD/MM/YYYY") }}</td>
          <td>{{ item.contract_date_end | moment("DD/MM/YYYY") }}</td>
          <td>
            <template v-if="item.contract_type == 1">
              <span class="badge bg-warning bg-opacity-10 text-black"
                ><i class="fa-solid fa-circle-small"></i> Gia hạn</span
              >
            </template>
            <template v-else>
              <span class="badge bg-success bg-opacity-10 text-success"
                ><i class="fa-solid fa-circle-small"></i> Mới</span
              >
            </template>
          </td>
          <td>{{ item.contract_expired_month }}</td>
          <td>
            <template v-if="item.is_renew == 0">
              <span class="badge text-bg-danger"> Chưa gia hạn</span>
            </template>
          </td>
          <td>
            <strong class="text-danger">{{ item.contract_total | formatamout }}</strong>
          </td>
          <td>
            <button
              class="btn btn-primary-soft btn-sm"
              @click="updateExpired(item, index)"
            >
              <i class="fa-regular fa-circle-check"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <modal-update-expired
      ref="modalUpdateExpired"
      @updateExpired="handelUpdateExpired"
    ></modal-update-expired>
  </div>
</template>

<script>
import ModalUpdateExpired from "@/extends/ModalUpdateExpired.vue";
import axios from "axios";
export default {
  components: {
    ModalUpdateExpired,
  },
  data() {
    return {
      contracts: [],
      index: 0,
    };
  },
  created() {
    this.getContracts();
  },
  methods: {
    async getContracts() {
      this.$econtract.showLoading();
      await axios.get("contractexpired").then((response) => {
        this.contracts = response.data;
        this.$econtract.hideLoading();
      });
    },

    updateExpired(item, index) {
      this.$refs.modalUpdateExpired.show(item);
      this.index = index;
    },
    handelUpdateExpired(item) {
      if (item.status == 200) {
        this.contracts.splice(this.index, 1);
        this.$toast.success("Cập nhật gia hạn thành công");
      }
    },
  },
};
</script>
